export default {
  name: 'chip',

  props: {
    close: Boolean,

    label: Boolean,

    outline: Boolean,

    small: Boolean,

    value: {
      type: Boolean,
      default: true
    }
  },

  computed: {
    classes () {
      return {
        'chip--label': this.label,
        'chip--outline': this.outline,
        'chip--small': this.small,
        'chip--removable': this.close
      }
    }
  }
}



