export default {
  name: 'alert',

  props: {
    dismissible: Boolean,

    error: Boolean,

    hideIcon: Boolean,

    icon: String,

    info: Boolean,

    success: Boolean,

    warning: Boolean,

    value: {
      type: Boolean,
      default: true
    }
  },

  computed: {
    classes() {
      return {
        'alert--dismissible': this.dismissible,
        'alert--error': this.error,
        'alert--info': this.info,
        'alert--success': this.success,
        'alert--warning': this.warning,
      }
    },

    mdIcon() {
      if (this.icon) {
        return this.icon
      }

      switch (true) {
        case this.error:
          return 'warning'
          break
        case this.info:
          return 'info'
          break
        case this.success:
          return 'check_circle'
          break
        case this.warning:
          return 'priority_high'
          break
      }
    }
  }
}



