import Toggleable from '../../mixins/toggleable'
import Storable from '../../mixins/storable'

export default {
  name: 'modal',

  mixins: [Storable, Toggleable],

  props: {
    bottom: Boolean,

    id: {
      type: String,
      required: true
    },

    origin: {
      type: String,
      default: 'bottom center'
    },

    transition: {
      type: String,
      default: 'v-modal-transition'
    }
  },

  computed: {
    classes () {
      return {
        'modal--bottom': this.bottom
      }
    }
  },

  methods: {
    closeConditional (e) {
      return this.$refs.modal === e.target || this.$refs.modal.contains(e.target)
    }
  }
}



