import Toggleable from '../../mixins/toggleable'
import Storable from '../../mixins/storable'

export default {
  name: 'menu',

  mixins: [Storable, Toggleable],

  data () {
    return {
      inputValue: {}
    }
  },

  props: {
    auto: Boolean,

    bottom: Boolean,

    id: {
      type: String,
      required: true
    },

    hover: Boolean,

    items: {
      type: Array,
      default: () => []
    },

    left: Boolean,

    maxHeight: {
      type: [String, Number],
      default: 'auto'
    },

    offsetX: Boolean,

    offsetY: Boolean,

    origin: {
      type: String,
      default: 'top left'
    },

    right: Boolean,

    top: Boolean,

    transition: {
      type: String,
      default: 'v-menu-transition'
    },

    value: {
      requied: false
    }
  },

  computed: {
    classes () {
      return {
        'menu--open-from-right': this.right
      }
    },

    computedOrigin () {
      if (this.index === -1 || !this.auto) {
        return this.origin
      }

      if (this.index === 0) {
        return 'top left'
      } else if (this.index === this.items.length - 1) {
        return 'bottom left'
      } else {
        return 'left center'
      }
    },

    index () {
      return this.items.indexOf(this.inputValue)
    },

    styles () {
      return {
        'max-height': isNaN(this.maxHeight) ? this.maxHeight : `${this.maxHeight}px`
      }
    }
  },

  watch: {
    active () {
      this.$emit('active', this.active)
    },
    
    value () {
      this.inputValue = this.value
    }
  },

  methods: {
    closeConditional (e) {
      return this.$el.contains(e.target)
    },

    updateValue (item) {
      this.commit(false)

      this.$emit('input', item)
    }
  }
}



