export default {
  name: 'breadcrumbs-item',
  
  props: {
    disabled: Boolean,

    item: {
      type: Object,
      required: true
    }
  },

  computed: {
    classes () {
      return {
        'breadcrumbs__item--disabled': this.disabled
      }
    }
  }
}



