export default {
  name: 'navbar',

  props: {
    fixed: Boolean,
    
    groupClass: {
      type: String,
      default: ''
    },

    items: {
      type: Array,
      default: () => []
    },

    ripple: Boolean,

    router: Boolean
  },

  computed: {
    classes () {
      return {
        'navbar--fixed': this.fixed
      }
    }
  }
}



