export default {
  name: 'select',
  
  data () {
    return {
      focused: false
    }
  },

  props: {
    defaultDisabled: {
      type: Boolean,
      default: true
    },
    
    defaultText: {
      type: String,
      default: 'Select...'
    },

    disabled: Boolean,

    id: {
      type: String,
      value: ''
    },

    label: {
      type: String,
      value: ''
    },

    multiple: Boolean,

    name: {
      type: String,
      value: ''
    },

    options: {
      type: Array,
      default: () => []
    },

    value: {
      required: false
    }
  },

  computed: {
    classes () {
      return {
        'input-group--dirty': true
      }
    }
  },

  mounted () {
    if (this.value) {
      this.$refs.select.value = this.value
    }
  },

  methods: {
    update () {
      if (!this.multiple) {
        this.$emit('input', this.$refs.select.value)
      } else {
        this.$emit('input', this.$refs.options.filter(i => i.selected).map(i => i.value))
      }
    }
  }
}



